'use strict';

import __ from '../../lib/util';

module.exports = class LightStateService extends __.baseService {
    constructor(model) {
        super(model);
    }

    _create(json) {
        json.tstamp = __.m.valueOf();
        return this.model.create(new this.model(json)).then(entry => {
            return entry;
        })
    }
}